.nav {
  font-family: Space Grotesk;

  &__menu {
    margin-bottom: 48px;
  }

  &__list{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;
    color: inherit;
    @include forAllLinks;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $color-main-accent;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}
